.c-icon{@apply .inline-block .align-middle .relative .fill-current;top:-2px;width:18px;height:18px}.c-icon.size-sm{width:.75rem;height:.75rem}.c-icon.size-base{width:1rem;height:1rem}.c-icon.size-lg{width:1.25rem;height:1.25rem}.c-icon.size-xl{width:1.5rem;height:1.5rem}.c-icon.size-2xl{width:1.75rem;height:1.75rem}.c-icon.size-3xl{width:2.25rem;height:2.25rem}.c-icon.size-4xl{width:3rem;height:3rem}.c-icon.size-5xl{width:4rem;height:4rem}