.c-lightswitch-input label{@apply .relative .block .select-none;width:46px;height:30px}.c-lightswitch-input label input{@apply .absolute .opacity-0;}.c-lightswitch-input label .slider{@apply .absolute .pin .cursor-pointer;background-color:#ccc;-webkit-transition:.4s;transition:.4s}.c-lightswitch-input label .slider:before{@apply .absolute .bg-white;content:"";height:26px;width:26px;bottom:2px;-webkit-transition:.1s;transition:.1s}body.ltr .c-lightswitch-input label .slider:before{left:2px}body.rtl .c-lightswitch-input label .slider:before{right:2px}.c-lightswitch-input label input:checked+.slider{background-color:#38c172}.c-lightswitch-input label input:focus+.slider{box-shadow:0 0 1px #38c172}.c-lightswitch-input label input:checked+.slider:before{-webkit-transform:translateX(16px);-ms-transform:translateX(16px);transform:translateX(16px)}.c-lightswitch-input label .slider.round{border-radius:34px}.c-lightswitch-input label .slider.round:before{border-radius:50%}.c-lightswitch-input label.disabled{opacity:.4}.c-lightswitch-input label.disabled .slider{@apply .cursor-default;}